// ==================Slider from library Swiper===================
.swiper-container {
    width: 100%;
    height: auto;
    padding-top: 30px;
    padding-bottom: 30px;
  }

.swiper-slide {
    flex-shrink: 0.07;
    background-position: center;
    background-size: cover;
    width: 500px;
    height: 300px;
} 
.swiper-pagination-bullet{
    background: #ff6b01;
}
.swiper-button-prev{
  color: #ff6b01;
  text-shadow: -2px -1px 0px rgb(107, 58, 2);
}
.swiper-button-next{
  color:  #ff6b01;
  text-shadow: -2px -1px 0px rgb(107, 58, 2);
}

// =====================Card-style====================
.swiper-card{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 10px;
    margin-bottom: 5px;
    width: 250px;
    height: auto;
    border: 3px rgb(139, 135, 4) solid;
    border-radius: 11px;
    text-align: center;
    background-color: #fff;
    color: black;
    
    @include font(14px, 24px);
}
.swiper-card-img{
  width: 100%;
  max-height: 250px;
  border-top-left-radius: 9px;
  border-top-right-radius: 8px;
  // margin-bottom: 10px;
  z-index: 1;
}
.swiper-card-img-bw{
  position: absolute;
  top:0;
  width: 100%;
  max-height: 250px;
  border-top-left-radius: 9px;
  border-top-right-radius: 8px;
  // margin-bottom: 10px;
  z-index: 2;
  opacity: 1;
  transition-property: opacity;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus{
    opacity: 0;
  };
}
.swiper-slide-active > div.swiper-card-firstsection > img.swiper-card-img-bw{
  opacity: 0;
}

.swiper-card-section{
  margin-bottom: 10px;
}
.swiper-card-name{
  padding-left: 10px;
  padding-right: 10px;
  font-weight: bold;
}
.swiper-card-profession{
  color: $film-card-titles-color;
  font-size:smaller;
  margin-bottom: 10px;
}
.swiper-card-characteristic{
  color: $footer-text-color;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 10px;
}
@media screen and (max-height: 654px) {
  .swiper-card-characteristic{
    display: none;
  }
}

// ======================modal-window styles==========
// .unvisible{
//   visibility: hidden;
// }
.footerModalClose{
    display: none;
}
    
    
#footer-modal > div > #footerCloseBtn{
    top: -4px;
    right: -4px;
    
}
#footerCloseBtn > span{
  color: #ff6b01;
  text-shadow: -2px 0px 0px rgb(8, 8, 8);

  transition-property: color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus{
    color: black;
  }
}
.footer-modal{
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(104, 100, 100);

    padding: 10px 0;
    overflow:hidden;
    @include font(14px, 24px);
    z-index: 99;

    
    background-image: url("/images/bg-image-footer-modal.jpg");
    background-size: cover;
    animation-duration: 0.55s;  /* Время работы анимации */
    animation-fill-mode: both;  /* Поведение анимации */
    animation-name: fadeInDown; /* Имя анимации */
}

@keyframes fadeInDown { /* Сама анимация */
    0% {                /* Старт анимации */
        opacity: 0;
        transform: translate3d(0px, -100%, 0px);
    }
    100% {              /* в финале анимации - финал это заданная 0.55s */
        opacity: 1;
        transform: none;
    }
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .footer-modal {
    background-image: url("/images/bg-image-footer-modal.jpg");
  }
}


.footer-modal-overlay{
    width: 100%;
    height: auto;
    background-color: transparent;

    // overflow:hidden;
    @include font(14px, 24px);
    
}
.modal-tittle{
    text-align: center;
    color: #ff6b01;
    padding-left: 10px;
    padding-right: 10px;
    border-bottom:2px solid rgb(78, 75, 75);
    text-shadow: -2px -1px 0px rgba(0, 0, 0, 1);
}

.footer-modal-close-btn{
    position: absolute;
    top: 20px;
    right: 20px;
    width: 130px;
    height: 130px;
    fill: #ff6b01;
    text-align: center;
}

// ==================socIcon list and link==============================

.socialicon-list {
    display: flex;
    justify-content: space-around;
    padding-left: 10px;
    padding-right: 10px;
    // padding-bottom: 5px;
}
.socialicon-list-link {
    display: flex;
    align-items: baseline;
    justify-content: center;
    padding-top: 5px;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    background-color: black;
  
    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      background-color: #ff6b01;
    }
    
    &:hover .social-icon,
    &:focus .social-icon {
      fill: black;
    }
}
  
  
.social-icon {
    width: 20px;
    height: 20px;
    fill: white;
    
    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}