.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
   width: 100%;
   position: fixed;
   bottom: 0;
   left: 0;
   right: 0;
   top: 0;
   opacity: 0;
   z-index: 9999;
   pointer-events: none;
  &.is-loader-open {
        opacity: 1;
         pointer-events: initial;
  }
}
.circle {
  animation: resize 1s linear infinite;
  position: relative;
  background: #ec8729ec;
  border-radius: 50%;
  height: 100px;
  width: 100px;
  &::after {
      animation: rotate 1s linear infinite;
      content: '';
      display: block;
      width: 120px;
      height: 120px;
      position: absolute;
      top: -10px;
      left: -10px;
      border: 10px solid transparent;
      border-top-color: #6D6875;
      border-radius: 50%;
  }
}
@keyframes rotate {
  0% { }
  70% { transform: rotate(250deg);}
  100% { transform: rotate(360deg); }
}
@keyframes resize {
  40% { transform: scale(1); }
  70% { transform: scale(1.1); }
}














